@mixin inputLight {
    outline: none;
    border: none;
    border-radius: 1rem;
    padding: 0.6rem;
    color: $txGray;
    font-size: 0.9rem;
    width: 350px;
    box-shadow: 0px 2px 1px -1px rgb(0 0 0 / 20%), 0px 1px 1px 0px rgb(0 0 0 / 14%), 0px 1px 3px 0px rgb(0 0 0 / 12%);
    max-width: 90%;
    box-sizing:border-box;
}